.theme-switch {
    display: flex;
    z-index: 0;
    position: absolute;
    margin-right: 5px;
    margin-top: -40px;

    @media screen and (max-width: 479px) {
        // margin-right: -55px;
    }
}

.theme-switch-icon {
    fill: #ff6b01;
    color: currentColor;
    height: 20px;
    width: 15px;
    margin-right: 5px;
    margin-left: 5px;
}

.theme-switch-control {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;
    width: 60px;
    height: 20px;
    border-radius: 50em;
    padding: 3px 0;

}

.theme-switch-toggle {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    height: 0;
    width: 0;
    pointer-events: none;
}

.theme-switch-track {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: transparent;
    user-select: none;
    background-color: #333;
    border-radius: inherit;
    z-index: 1;
    cursor: pointer;
}

.theme-switch-marker {
    position: relative;
    background-color: #ffffff;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    z-index: 2;
    pointer-events: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
    transform: translateX(3px);
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.theme-switch-toggle:checked+.theme-switch-track {
    background-color: #d3d3d4;
}

.theme-switch-toggle:checked~.theme-switch-marker {
    transform: translateX(35px);
}

.theme-switch-toggle:hover+.theme-switch-track,
.theme-switch-toggle:active+.theme-switch-track {
    box-shadow: 0 0 0 3px #ff6b01;
}

.dark {
    background-color: #333333;
    color: #ffffff;

    & footer {
        background-color: #757575;
    }

    & #tui-pagination-container {
        background-color: #757575;

        & .tui-first:hover,
        & .tui-prev:hover,
        & .tui-next:hover,
        & .tui-last:hover {
            background-color: #9f9f9f;
        }
    }

    & #tui-pagination-container .tui-first:hover,
    & #tui-pagination-container .tui-prev:hover,
    & #tui-pagination-container .tui-next:hover,
    & #tui-pagination-container .tui-last:hover {
        background-color: #9f9f9f;
    }

    & .tui-ico-first,
    & .tui-ico-prev,
    & .tui-ico-next,
    & .tui-ico-last {
        filter: brightness(1)contrast(1.2);
    }

    & .film-card__title {
        color: #ffffff;
    }

    & .film-card {
        box-shadow: 0px 1px 1px rgba(240, 240, 240, 0.12), 0px 4px 4px rgba(240, 240, 240, 0.06), 1px 4px 6px rgba(240, 240, 240, 0.16);
    }

    & .film-card:hover {
        box-shadow: 0px 3px 3px rgba(240, 240, 240, 0.18), 0px 8px 8px rgba(240, 240, 240, 0.12), 3px 8px 12px rgba(240, 240, 240, 0.22);
    }
}