.modal-container {
  position: fixed;
  width: 280px;
  max-height: 900px;

  top: 50%;
  left: 50%;

  display: block;
  transform: translate(-50%, -50%) scaleY(1);
  scrollbar-width: none;

  border: 1px solid #000000;
  background-color: white;
  border-radius: 5px;

  body.dark & {
    border: 2px solid #ff6b08;
    background-color: #333;
  }

  transition: transform 400ms linear;

  padding: 48px 16px;

  overflow-y: scroll;
  overflow-x: hidden;

  @media screen and (min-width: 480px) {
    width: 380px;
  }

  @media screen and (max-width: 480px) {
    height: 97vh;
  }

  @media screen and (min-width: 768px) {
    top: 50%;
    left: 50%;
    width: 700px;
    display: flex;
    min-height: 420px;
    max-height: 550px;
    padding: 40px 16px;

    overflow-y: hidden;
  }

  @media screen and (min-width: 1024px) {
    width: 815px;
    height: 560px;
  }
}

.modal-container::-webkit-scrollbar {
  display: none;
}

.cross {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  color: rgb(113, 110, 105);
  border: none;
  background-color: transparent;

  position: absolute;
  z-index: 1;
  top: 0%;
  right: 1%;
  transform: rotate(45deg);

  transition: color 250ms linear;

  &:hover {
    color: orange;
  }
}

.modal-img-container {
  position: relative;
  margin-bottom: 20px;

  @media screen and (min-width: 480px) {
    margin-left: 54px;
    margin-right: 54px;
  }

  @media screen and (min-width: 768px) {
    margin-right: 52px;
    margin-left: 44px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1024px) {
    margin-right: 53px;
    margin-left: 37px;
  }
}

.film-name {
  position: relative;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 30px;
  line-height: 35px;

  color: #000000;

  body.dark & {
    color: #ffffff;
  }
}

.film-description {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  width: 100%;
  max-height: 200px;
  color: #000000;
  overflow-y: scroll;

  @media screen and (min-width: 768px) {
    max-height: 180px;
  }

  @media screen and (min-width: 1024px) {
    max-height: 160px;
  }
}

.film-description::-webkit-scrollbar {
  display: none;
}

.modal-btn-wrap {
  display: flex;
  justify-content: center;

  @media screen and (min-width: 768px) {
    justify-content: flex-start;
  }
}

.modal-btn:first-child {
  margin-right: 15px;
}

.modal-btn {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  padding: 12px 10px 12px;
  border-radius: 5px;
  background: #ffffff;
  color: #000000;
  border: 1px solid #000000;
  cursor: pointer;
}

.modal-btn--active {
  background-color: #ff6b01;
  color: white;
  border: none;
}

.modal-btn.active {
  background-color: #ff6b01;
  color: white;
  border: none;
}

.modal-table {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  &__attribute {
    margin-top: 0;
    margin-bottom: 8px;
    margin-right: 40px;
    color: #8c8c8c;
    white-space: nowrap;
  }

  &__value {
    margin-top: 0;
    margin-bottom: 8px;
  }

  &__vote {
    background: #ff6b01;
    border-radius: 5px;
    color: #ffffff;
    padding-left: 5px;
    padding-right: 5px;
    margin-right: 1px;
  }

  &__votes {
    background: #f7f7f7;
    color: #000;
    border-radius: 5px;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: 1px;
  }
}

.film-about {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.33;
}

.modal-img {
  width: 240px;
  height: 357px;
  border-radius: 5px;

  @media screen and (min-width: 480px) {
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (min-width: 768px) {
    max-width: 280px;
    max-height: 373px;
  }

  @media screen and (min-width: 1024px) {
